body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input, textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0;
}

fieldset, img {
  border: 0;
}
img {
  color: #ccc;
}
a, a:hover {
  text-decoration: none;
}
h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: 300;
}